.footer_desktop_button_open {
  background: linear-gradient(rgba(255, 255, 0, 1),  rgba(212, 144, 57, 1) );
  border: 0px solid black;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.8), 0 0 15px 0 rgba(0, 0, 0, 0.8);
  color: black !important;
  cursor: pointer;
  display: flex;
  font-family: "Staatliches";
  font-size: calc(20px + (40 - 20) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  justify-content: center;
  order: 2;
  text-align: center;
  text-shadow: 1px 1px 2px #000;
  transition: border-radius, border, box-shadow, color .25s ease-in-out 0s, width .1s ease-in-out 0s;
  width: calc(100% + 1px);
  z-index: 500;
}

.footer_desktop_button_open:active {
  animation: scale_out .1s ease-in-out 1;
}

.footer_desktop_button_open:focus {
  outline: -webkit-focus-ring-color auto 0px;
}

.footer_desktop_button_closed {
  background: linear-gradient( rgba(50, 50, 50, 1) 10%,rgba(0, 0, 0, 1) 100%);
  border: 1px solid grey;
  border-radius: 15px 15px 0px 0px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.8), 0 0 15px 0 rgba(0, 0, 0, 0.8);
  color: white;
  cursor: pointer;
  display: flex;
  font-family: "Staatliches";
  font-size: calc(20px + (40 - 20) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  justify-content: center;
  order: 2;
  text-align: center;
  text-shadow: 1px 1px 2px #000;
  transition: border-radius, border, box-shadow, color .25s ease-in-out 0s, width .1s ease-in-out 0s;
  width: calc(60px + (120 - 60) * ((100vw - 500px) / (1920 - 500)));
  z-index: 500;
}

.footer_desktop_button_closed:active {
  animation: scale_out .1s ease-in-out 1;
}

.footer_desktop_button_closed:focus {
  outline: -webkit-focus-ring-color auto 0px;
}

.footer_desktop_button_img {
  height: calc(20px + (40 - 20) * ((100vw - 500px) / (1920 - 500)));
  padding: 5px;
  width: calc(20px + (40 - 20) * ((100vw - 500px) / (1920 - 500)));
}